<template>
    <div
        ref="bar"
        class="bar"
        @mouseenter="scaleBar_enter_callback"
        @mouseleave="scaleBar_leave_callback"
    >
        <div class="bar__container">
            <Bar
                v-for="(to, index) in data"
                :key="to + index"
                :scaleBar="scaleBar"
                :label_color="colors[index]"
                :bar_style="{ width: '20px' }"
                :to="to"
                :top_label="index % 2 ? false : true"
                :fragment_style="{
                    width: '20px',
                    backgroundColor: colors[index]
                }"
                :to_array="data"
                @show_circle="callback_circle_completed"
                @hide_circle="callback_circle_start"
            ></Bar>
            <div
                :class="[
                    'bar__circle',
                    change_circle ? 'bar__circle--solid' : ''
                ]"
            ></div>
        </div>
        <transition name="FadeIn">
            <div v-show="show_leyenda" class="bar__leyenda">
                <span class="leyenda__tag">{{ tag }}</span>
                <span
                    class="leyenda__leyenda"
                    v-for="(nombre, index) in leyendas"
                    :key="data[index] + index + nombre"
                    :style="'color:' + colors[index]"
                    >{{ nombre }}</span
                >
            </div>
        </transition>
    </div>
</template>

<script>
import Bar from "@/components/bar-plot/bar.vue"
export default {
    components: {
        Bar
    },
    name: "barChart",
    props: {
        data: Array,
        colors: Array,
        leyendas: Array,
        tag: String
    },
    data() {
        return {
            change_circle: false,
            show_leyenda: false,
            scaleBar: false,
            is_mobile: false,
            observer: null
        }
    },
    methods: {
        scaleBar_enter_callback() {
            this.scaleBar = true
        },

        scaleBar_leave_callback() {
            this.scaleBar = false
        },

        callback_circle_completed(circle) {
            if (this.scaleBar) {
                this.change_circle = circle
                this.show_leyenda = circle
            }
        },

        callback_circle_start() {
            if (!this.scaleBar) {
                this.change_circle = false
                this.show_leyenda = false
            }
        },

        onElementObserved(entries) {
            if (this.is_mobile) {
                entries.forEach(({ isIntersecting }) => {
                    if (!isIntersecting) {
                        this.scaleBar = false
                        return
                    }
                    this.scaleBar = true
                })
            }
        },

        handleResize() {
            const isMobile = window.matchMedia(
                "(max-width: 720px), (max-height: 410px)"
            )
            this.is_mobile = isMobile.matches ? true : false
        }
    },
    mounted() {
        window.addEventListener("resize", this.handleResize)
        this.handleResize()

        this.observer = new IntersectionObserver(this.onElementObserved, {
            root: this.$root.$el,
            rootMargin: "-70px 0px -50px 0px",
            threshold: 1
        })
        this.observer.observe(this.$el)
    },
    beforeDestroy() {
        this.observer.disconnect()
    }
}
</script>

<style lang="scss" src="./barChart.scss" scoped></style>
