<template>
    <div ref="barVertical" class="barVertical">
        <div class="barVertical__container">
            <BarVertical
                v-for="(to, index) in data"
                :key="random_keys[index]"
                :scaleBarVertical="scaleBarVertical"
                :label_color="colors[index]"
                :barVertical_style="{ height: '20px' }"
                :to="to"
                :top_label="index % 2 ? false : true"
                :fragment_style="{
                    height: '20px',
                    backgroundColor: colors[index]
                }"
                :to_array="data"
            ></BarVertical>
        </div>
        <transition name="FadeIn">
            <div class="barVertical__leyenda">
                <span class="leyenda__tag">{{ tag }}</span>
            </div>
        </transition>
    </div>
</template>

<script>
import BarVertical from "@/components/bar-vertical-plot/barVertical.vue"
export default {
    components: {
        BarVertical
    },
    name: "barVerticalChart",
    props: {
        data: Array,
        colors: Array,
        leyendas: Array,
        tag: String
    },
    data() {
        return {
            show_leyenda: false,
            scaleBarVertical: false,
            is_mobile: false,
            observer: null,
            random_keys: []
        }
    },
    methods: {
        onElementObserved(entries) {
            entries.forEach(({ isIntersecting }) => {
                if (!isIntersecting) {
                    return
                }
                this.scaleBarVertical = true
            })
        },

        makeid() {
            let result = ""
            let characters =
                "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789"
            let charactersLength = characters.length
            for (let i = 0; i < 16; i++) {
                result += characters.charAt(
                    Math.floor(Math.random() * charactersLength)
                )
            }
            return result
        }
    },
    mounted() {
        for (let index = 0; index < this.data.length; index++) {
            this.random_keys.push(this.makeid())
        }

        this.observer = new IntersectionObserver(this.onElementObserved, {
            root: this.$root.$el,
            rootMargin: "50px 0px 50px 0px",
            threshold: 1
        })
        this.observer.observe(this.$el)
    },
    beforeDestroy() {
        this.observer.disconnect()
    }
}
</script>

<style lang="scss" src="./barVerticalChart.scss" scoped></style>
