<template>
    <div class="barVertical__column" :style="local_barVertical_style">
        <number
            v-show="show_label"
            @complete="callback_complete"
            @start="callback_start"
            :style="'color:' + label_color"
            ref="barVertical"
            animationPaused
            :class="[
                'barVertical__number',
                top_label ? 'top_position' : 'bottom_position'
            ]"
            :from="scaleBarVertical ? to : from"
            :to="scaleBarVertical ? from : to"
            :format="theFormat"
            :duration="duration"
            easing="Power1.easeOut"
        />
        <div class="barVertical__fragment" :style="local_fragment_style"></div>
    </div>
</template>

<script>
export default {
    name: "BarVertical",
    props: {
        top_label: {
            type: Boolean,
            default: true
        },
        scaleBarVertical: {
            type: Boolean,
            default: false
        },
        from: {
            type: Number,
            default: 0
        },
        to: {
            type: Number,
            default: 60
        },
        to_array: {
            type: Array,
            default: () => []
        },
        barVertical_style: {
            type: Object,
            default: () => ({})
        },
        fragment_style: {
            type: Object,
            default: () => ({})
        },
        duration: {
            type: Number,
            default: 0.7
        },
        standar_height: {
            type: String,
            default: "20px"
        },
        label_color: {
            type: String,
            default: "#000"
        }
    },
    data() {
        return {
            show_label: false,
            local_barVertical_style: this.barVertical_style,
            local_fragment_style: this.fragment_style
        }
    },
    watch: {
        scaleBarVertical: function() {
            this.scaleBarVertical_callback()
        }
    },
    methods: {
        theFormat(number) {
            return number.toLocaleString("de-DE", {
                maximumFractionDigits: 0
            })
        },

        callback_complete() {
            if (!this.scaleBarVertical) {
                this.show_label = false
            }

            this.$emit("show_circle", this.scaleBarVertical)
        },

        callback_start() {
            this.$emit("hide_circle", this.scaleBarVertical)
        },

        scaleBarVertical_callback() {
            if (this.scaleBarVertical) {
                this.show_label = true
                this.$refs.barVertical.play()

                this.$set(this.local_fragment_style, "height", "100%")
                this.$set(
                    this.local_barVertical_style,
                    "height",
                    "calc(" +
                        (
                            (this.to * 100) /
                            this.to_array.reduce((a, b) => a + b, 0)
                        )
                            .toFixed(0)
                            .toString() +
                        "% + 50px)"
                )
            } else {
                this.$refs.barVertical.restart()

                this.$set(this.local_fragment_style, "height", "100%")
                this.$set(
                    this.local_barVertical_style,
                    "height",
                    this.standar_height
                )
            }
        }
    }
}
</script>

<style lang="scss" src="./barVertical.scss" scoped></style>
