<template>
    <div class="bar__column" :style="local_bar_style">
        <number
            v-show="show_label"
            @complete="callback_complete"
            @start="callback_start"
            :style="'color:' + label_color"
            ref="bar"
            animationPaused
            :class="[
                'bar__number',
                top_label ? 'top_position' : 'bottom_position'
            ]"
            :from="scaleBar ? to : from"
            :to="scaleBar ? from : to"
            :format="theFormat"
            :duration="duration"
            easing="Power1.easeOut"
        />
        <div class="bar__fixed-div"></div>
        <div class="bar__fragment" :style="local_fragment_style"></div>
        <div class="bar__fixed-div"></div>
    </div>
</template>

<script>
export default {
    name: "Bar",
    props: {
        top_label: {
            type: Boolean,
            default: true
        },
        scaleBar: {
            type: Boolean,
            default: false
        },
        from: {
            type: Number,
            default: 0
        },
        to: {
            type: Number,
            default: 60
        },
        to_array: {
            type: Array,
            default: () => []
        },
        bar_style: {
            type: Object,
            default: () => ({})
        },
        fragment_style: {
            type: Object,
            default: () => ({})
        },
        duration: {
            type: Number,
            default: 0.7
        },
        standar_width: {
            type: String,
            default: "20px"
        },
        label_color: {
            type: String,
            default: "#000"
        }
    },
    data() {
        return {
            show_label: false,
            local_bar_style: this.bar_style,
            local_fragment_style: this.fragment_style
        }
    },
    watch: {
        scaleBar: function() {
            this.scaleBar_callback()
        }
    },
    methods: {
        theFormat(number) {
            return number.toLocaleString("de-DE", {
                maximumFractionDigits: 0
            })
        },

        callback_complete() {
            if (!this.scaleBar) {
                this.show_label = false
            }

            this.$emit("show_circle", this.scaleBar)
        },

        callback_start() {
            this.$emit("hide_circle", this.scaleBar)
        },

        scaleBar_callback() {
            if (this.scaleBar) {
                this.show_label = true
                this.$refs.bar.play()

                this.$set(this.local_fragment_style, "width", "100%")
                this.$set(
                    this.local_bar_style,
                    "width",
                    ((this.to * 100) / this.to_array.reduce((a, b) => a + b, 0))
                        .toFixed(0)
                        .toString() + "%"
                )
            } else {
                this.$refs.bar.restart()

                this.$set(this.local_fragment_style, "width", "100%")
                this.$set(this.local_bar_style, "width", this.standar_width)
            }
        }
    }
}
</script>

<style lang="scss" src="./bar.scss" scoped></style>
